:root {
  --speed: 300ms;
  --time-function: ease-in-out;
  --vh: 1vh;
}

/*=======================START-MEDIA=====================*/
/*=======================END-MEDIA=====================*/
/* media breakpoint mixin */
.buttons {
  display: flex;
}

@supports (column-gap: 16px) {
  .buttons {
    column-gap: 16px;
  }
}

@supports not (column-gap: 16px) {
  .buttons {
    margin-right: calc( 0px - (16px / 2));
    margin-left: calc( 0px - (16px / 2));
  }
  .buttons > * {
    margin-right: calc(16px / 2);
    margin-left: calc(16px / 2);
  }
}

@media only screen and (max-width: 1200px) {
  .buttons {
    flex-direction: column;
  }
  @supports (row-gap: 11px) {
    .buttons {
      row-gap: 11px;
    }
  }
  @supports not (row-gap: 11px) {
    .buttons {
      margin-top: -11px;
    }
    .buttons > * {
      margin-top: 11px;
    }
  }
}

@media only screen and (max-width: 1200px) {
  .buttons__item {
    display: flex;
    justify-content: center;
  }
}

.content-page {
  padding-top: 54px;
}

@media only screen and (max-width: 1200px) {
  .content-page {
    padding-top: 50px;
  }
}

@media only screen and (max-width: 540px) {
  .content-page {
    padding-top: 15px;
  }
}

.content-page__title {
  max-width: 380px;
  text-wrap: normal;
}

@media only screen and (max-width: 1200px) {
  .content-page__title {
    max-width: 280px;
  }
}

@media only screen and (max-width: 540px) {
  .content-page__title {
    max-width: 180px;
  }
}

.content-page__title:not(:last-child) {
  margin-bottom: 14px;
}

.content-page__text {
  max-width: 651px;
}

.contact__section {
  padding-top: 43px;
}

@media only screen and (max-width: 1200px) {
  .contact__section {
    padding-top: 70px;
  }
}

@media only screen and (max-width: 540px) {
  .contact__section {
    padding-top: 25px;
  }
}

@media only screen and (max-width: 1200px) {
  .contact__row {
    flex-direction: column-reverse;
  }
}

@media only screen and (max-width: 1200px) {
  .contact__item {
    padding-left: 44px;
    padding-right: 44px;
  }
  .contact__item .row__item-right-container > * {
    margin-left: 0px;
    margin-right: 0px;
  }
}

@media only screen and (max-width: 540px) {
  .contact__item {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.contact__map {
  line-height: 0px;
}

.contact__map iframe {
  border: 0px !important;
}

@media only screen and (min-width: 1200px) {
  .contact__map iframe {
    height: 100%;
  }
}

@media only screen and (max-width: 1200px) {
  .contact__map {
    padding: 0px;
  }
}

.contact__heading {
  padding-left: 23px;
  margin-bottom: 42px;
}

@media only screen and (max-width: 1200px) {
  .contact__heading {
    padding-left: 0px;
    margin-bottom: 52px;
  }
}

@media only screen and (max-width: 540px) {
  .contact__heading {
    margin-bottom: 24px;
  }
}

@media only screen and (max-width: 1200px) {
  .contact__cards {
    flex-direction: column;
  }
}

.contact__cards:not(:last-child) {
  margin-bottom: 32px;
}

@media only screen and (max-width: 1200px) {
  .contact__cards .card:not(:last-child) {
    margin-bottom: 20px;
  }
}

@media only screen and (max-width: 540px) {
  .contact__cards .card:not(:last-child) {
    margin-bottom: 24px;
  }
}

.contact__form {
  margin-top: 10px;
  margin-bottom: -5px;
}

.contact__form form,
.contact__form form p {
  align-items: flex-start;
}

.contact__form .form__button {
  margin: 12px 0px 0px;
}

.contact__button-wrapper {
  padding: 33px;
}

@media only screen and (max-width: 1200px) {
  .contact__button-wrapper {
    padding-top: 52px;
    padding-bottom: 72px;
  }
}

@media only screen and (max-width: 540px) {
  .contact__button-wrapper {
    border-radius: 0px;
    padding-left: 20px;
    padding-right: 20px;
  }
  .contact__button-wrapper:nth-child(n) {
    margin-left: -20px;
    margin-right: -20px;
  }
}

.contact__button-title {
  margin-bottom: 39px;
  text-align: center;
}

@media only screen and (min-width: 1201px) {
  .contact__button-title {
    display: none;
  }
}

.contact__item .button {
  justify-content: center;
  text-align: center;
}

.contact__page-heading {
  margin-bottom: 86px;
}

@media only screen and (max-width: 1200px) {
  .contact__page-heading {
    margin-bottom: 50px;
  }
}

@media only screen and (max-width: 540px) {
  .contact__page-heading {
    margin-bottom: 42px;
  }
}
